import React from 'react';
import { htmlEntities, SectionConfigLayout } from './utils';
import BaseSectionConfig from './BaseSectionConfig';


class LaParisBanner1Style1FormItem extends React.Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        this.props.onFormItemChange(event.target.name, event.target.value);
    }

    render() {
        const { img, title, body, button, link } = this.props;
        return (
            <form>
                <div className="form-group">
                    <label htmlFor="img">Image URL</label>
                    <input type="text" className="form-control" id="img" name="img" value={img} onChange={this.handleChange} aria-describedby="imgHelp" placeholder="Enter the banner image URL" />
                    <small id="imgHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
                </div>
                <div className="form-group">
                    <label htmlFor="title">Title</label>
                    <input type="text" className="form-control" id="title" name="title" value={title} onChange={this.handleChange} placeholder="Enter the title text" />
                </div>
                <div className="form-group">
                    <label htmlFor="body">Body</label>
                    <textarea className="form-control" id="body" name="body" value={body} onChange={this.handleChange} placeholder="Enter the body text" />
                </div>
                <div className="form-group">
                    <label htmlFor="button">Button</label>
                    <input type="text" className="form-control" id="button" name="button" value={button} onChange={this.handleChange} placeholder="Enter the button text" />
                </div>
                <div className="form-group">
                    <label htmlFor="link">Link</label>
                    <input type="text" className="form-control" id="link" name="link" value={link} onChange={this.handleChange} placeholder="Enter the link URL" />
                </div>
            </form>
        );
    }
}

class LaParisBanner1Style1FormGeneral extends React.Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        this.props.onFormGeneralChange(event.target.name, event.target.value);
    }

    render() {
        const { items_count } = this.props;
        return (
            <form>
                <div className="form-group">
                    <label htmlFor="items_count">Number of banner items</label>
                    <input type="number" min="0" max="100" className="form-control" id="items_count" name="items_count" value={items_count} onChange={this.handleChange} placeholder="Enter number of banner items" />
                </div>
            </form>
        );
    }
}

class LaParisBanner1Style1 extends BaseSectionConfig {
    defaultTitle = "Style 1 / 3 Banners";
    scopeName = "LaParisBanner1Style1";

    constructor(props) {
        super(props);

        this.state = this.getStateFromLocalStorage() || {
            items: [
                { 
                    img: 'https://tvlgiao.github.io/bigcommerce-themes/laparis/demo/images/banner01.jpg', 
                    title: 'The Woman Collection', 
                    body: 'Mauris at bibendum ex, sit amet ornare quam. Aenean interdum hendrerit lectus sagittis turpis sit amet neque varius.', 
                    button: 'View Categories', 
                    link: '#'
                },
                { 
                    img: 'https://tvlgiao.github.io/bigcommerce-themes/laparis/demo/images/banner02.jpg', 
                    title: 'Hello Summer', 
                    body: 'HOT DISCOUNTS OF THIS WEEKEND', 
                    button: 'Look Book', 
                    link: '#'
                },
                { 
                    img: 'https://tvlgiao.github.io/bigcommerce-themes/laparis/demo/images/banner03.jpg', 
                    title: 'The Main Style', 
                    body: '', 
                    button: 'View Collection', 
                    link: '#'
                },
            ]
        };
    }

    render() {
        const data = Buffer.from(JSON.stringify(this.state)).toString('base64');
        return (
            <SectionConfigLayout elClass="LaParisBanner1Style1" title={this.title} 
                panels={[
                    {
                        title: "General",
                        element: <LaParisBanner1Style1FormGeneral onFormGeneralChange={this.onFormGeneralChange} heading={this.state.heading} items_count={this.state.items.length} />,

                    },
                    ...this.state.items.map((item, index) => ({
                        title: `Banner ${index + 1}`,
                        element: <LaParisBanner1Style1FormItem onFormItemChange={this.onFormItemChange.bind(this, index)} {...item} />,
                    })),
                ]}

                code={`<div class="u-hiddenVisually" data-local-banner-position="banner-laparis-home-bnr-s1-1" data-template="home_bnr_s1_1" data-params="${data}">DO NOT REMOVE THIS LINE</div>`}

                legacyCode={``.concat(
                    `<div class="emthemesModez-bannersContainer emthemesModez-bannersContainer--laparis1">`,
                    ...this.state.items.map((item, index) => `
                        <!-- Banner ${index + 1} -->
                        <div class="emthemesModez-banner emthemesModez-banner--laparis1">
                            <figure class="emthemesModez-banner-figure">
                                <a href="${htmlEntities(item.link)}"><img class="emthemesModez-banner-image" src="${htmlEntities(item.img)}" alt="${htmlEntities(item.title)}" title="${htmlEntities(item.title)}" /></a>
                            </figure>
                            <figcaption class="emthemesModez-banner-figcaption">
                                <div class="emthemesModez-banner-figcaption-body">
                                    <h4 class="emthemesModez-banner-figcaption-title">${htmlEntities(item.title)}</h4>
                                    <div class="emthemesModez-banner-figcaption-text">${htmlEntities(item.body)}</div>
                                    <a href="${htmlEntities(item.link)}" class="button button--primary emthemesModez-banner-figcaption-button">${htmlEntities(item.button)}</a>
                                </div>
                            </figcaption>
                        </div>`),
                    `
                    </div>`
                )}
            />
        );
    }
}

export default LaParisBanner1Style1;
